class HistoricalImage {
    constructor() {
        this.camera = undefined;
        this.projectedPoints = [];
    }

    setCamera(camera, geometries) {
        this.camera = camera;

        // Compute camera matrices manually
        // TODO fix bug related to inverse projection matrix
        var inverseProj = new THREE.Matrix4().getInverse(camera.projectionMatrix);
        var world = camera.matrixWorld.clone();
        //Same as: new THREE.Vector3(  0,  0, -1).unproject(camera);
        var origin = camera.position.clone();

        // Target and four corners of the far plane from the camera (normalized)
        var dirs = [];
        dirs.push(new THREE.Vector3(  0,  0,  1).applyMatrix4(inverseProj).applyMatrix4(world).sub(origin).normalize());  // target
        dirs.push(new THREE.Vector3( -1, -1,  1).applyMatrix4(inverseProj).applyMatrix4(world).sub(origin).normalize());  // left bottom
        dirs.push(new THREE.Vector3( -1,  1,  1).applyMatrix4(inverseProj).applyMatrix4(world).sub(origin).normalize());  // left top
        dirs.push(new THREE.Vector3(  1,  1,  1).applyMatrix4(inverseProj).applyMatrix4(world).sub(origin).normalize());  // right top
        dirs.push(new THREE.Vector3(  1, -1,  1).applyMatrix4(inverseProj).applyMatrix4(world).sub(origin).normalize());  // right bottom

        var ray = new THREE.Raycaster();

        // Picks their projected position in the world
        dirs.forEach(dir => {
            // Creates a ray with camera as origin and the specific direction
            ray.set(origin, dir);
            var rayIntersects = ray.intersectObjects(geometries, true);
            if(rayIntersects.length > 0) {
                var firstIntersect = rayIntersects[0].point || null;
                this.projectedPoints.push(firstIntersect);
            } else {
                this.projectedPoints.push(null);
            }
        });
    }
}

export default HistoricalImage;